@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  outline: none;
}

:root {
  --color-neutral-100: #ffffff;
  --color-neutral-200: #f7f9fc;
  --color-neutral-300: #edf1f7;
  --color-neutral-400: #e4e9f2;
  --color-neutral-500: #c5cee0;
  --color-neutral-600: #8f9bb3;
  --color-neutral-700: #2e3a59;
  --color-neutral-800: #222b45;
  --color-neutral-900: #1a2138;
  --color-neutral-1000: #151a30;
  --color-neutral-1100: #101426;
}

html,
body {
  height: 100%;
  font-size: 16px;
  padding: 0;
  margin: 0;
}

body {
  line-height: 1.5;
  font-family: 'Poppins', sans-serif;
  padding: env(safe-area-inset);
  color: var(--color-neutral-800);
  display: flex;
  justify-content: center;
  align-items: center;
  height: min-content;
  min-height: 100%;
}

.container {
  padding: 3rem clamp(1.5rem, 3%, 2rem);
  margin: 0 auto;
  max-width: 1280px;
  width: 100%;
}

h1 {
  font-size: clamp(1.5rem, 5vw, 2rem);
  margin: 0 0 1em 0;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(17rem, 1fr));
  grid-gap: 2rem;
}

.card {
  height: 9.5625rem;
  border-radius: 0.625rem;
  overflow: hidden;
  position: relative;
  padding: 1rem;
  color: var(--color-neutral-100);
  text-decoration: none;
  box-shadow: 0 0.625rem 1.25rem rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: 0.3s ease-out;
}

.card:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(
    180deg,
    transparent 50%,
    var(--color-neutral-1100) 150%
  );
  mix-blend-mode: darken;
}

.card .card__image {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  width: 110%;
  height: 110%;
  filter: brightness(0.75) saturate(1.2) contrast(0.85);
  z-index: -1;
  transform: scale(1);
  transition: 0.3s ease-out;
}

.card .card__title {
  font-size: 1.5rem;
  font-weight: 700;
}

.card .card__description {
  font-size: 0.875rem;
  font-weight: 300;
}

@media (hover: hover) {
  .card:hover {
    transform: scale(1.05);
    box-shadow: 0 0.9375rem 1.875rem rgba(0, 0, 0, 0.5);
  }

  .card:hover .card__image {
    transform: translateY(-0.5rem);
  }
}
